
import { FC } from 'react';
import { useAppSelector } from '../../utils/hooks';
import { CheckSvg } from "../../images/check";

import s from './OrderDetails.module.css';

export const OrderDetails: FC = () => {
    const number = useAppSelector(store => store.order.number);
    return (
        <section className={`${s.content} mt-20 mb-20`}>
            <span className={`${s.orderNumber} text text_type_digits-large`}>{number}</span>
            <span className='mt-8 mb-15 text text_type_main-medium'> </span>
            <CheckSvg />
            <span className='mt-15 mb-2 text text_type_main-default'>   </span>
            <span className='text text_type_main-default text_color_inactive'>    </span>
        </section>
    )
}