import { useState, ChangeEvent } from 'react';
import { Input, Button }  from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, Redirect, useLocation } from 'react-router-dom';
import { restoreEmail } from '../services/actions/User';
import { useAppThunkDispatch, useAppSelector } from '../utils/hooks';

import s from './pages.module.css';

export function ForgotPassword() {
  const [email, setEmail] = useState('');
  const success = useAppSelector(store => store.user.resetPasswordSuccess);
  const dispatch = useAppThunkDispatch();

  const { pathname } = useLocation();

  const handleSendEmail = (e: ChangeEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(restoreEmail(email));
  }
  const handleChangeEmail = (e: ChangeEvent<HTMLInputElement>) => {
    setEmail(e.currentTarget.value);
  }

  if (success) {
    return <Redirect
    to={{
      pathname: '/reset-password',
      state: { from: pathname }
    }}
  />
  }

 return (
  <div className={s.wrapForm}>
     <h2 className="text text_type_main-medium pb-6"> </h2>
      <form className={`${s.form} mb-20`} onSubmit={handleSendEmail}>
        <Input
            type={'email'}
            placeholder={' e-mail'}
            onChange={handleChangeEmail}
            value={email}
            name={'email'}
          />
          <Button type="primary" size="medium">
            
          </Button>
      </form>
      <p className='text text_type_main-default'>
        <span className='text_color_inactive'> ?</span> 
        <Link to='/login'> </Link>
      </p>
  </div>
)
}